import React, { useState, useEffect } from "react";
import fetch from "isomorphic-unfetch";
import Router, { useRouter } from "next/router";
import Head from "next/head";

function Blogpage(props) {
  // const title = "title goes here";
  const [blogData, setBlogData] = useState(props["jsonData"]);
  // const { title = "sdfdsf" } = props["jsonData"];

  async function refresh() {
    const refreshedProps = await fetchData();

    setBlogData(refreshedProps["jsonData"]);
  }

  useEffect(() => {
    refresh();
  }, []);

  return (
    <div>
      <Head>
        <title>{blogData["title"]}</title>
        <meta
          name="description"
          content={blogData["title"] + "description goes here"}
        />
      </Head>
      <p>{JSON.stringify(blogData)}</p>
    </div>
  );
}

async function fetchData() {
  const id = Router["router"] && Router["router"]["asPath"].split("/")[2];
  console.log("refreshedProps", id);

  // ${router['router']['asPath'].split('/')[2]
  const res = await fetch(`https://jsonplaceholder.typicode.com/todos/${id}`);
  const json = await res.json();

  return { jsonData: json };
}

Blogpage.getInitialProps = fetchData;

export default Blogpage;
